import { IOConnectBrowser } from "../../browser";
import { activeNotificationsCountChangeDecoder, allNotificationsDataDecoder, notificationEventPayloadDecoder, notificationSetStateRequestDecoder, notificationsConfigurationProtocolDecoder, permissionQueryResultDecoder, permissionRequestResultDecoder, raiseNotificationDecoder, raiseNotificationResultDecoder, simpleNotificationDataDecoder, simpleNotificationSelectDecoder, strictNotificationsConfigurationProtocolDecoder } from "../shared/decoders";
import { BridgeOperation } from "../shared/types";

export type NotificationsOperationTypes = "raiseNotification" | "requestPermission" | "notificationShow" | "notificationClick" | "getPermission" |
    "list" | "notificationRaised" | "notificationClosed" | "click" | "clear" | "clearAll" | "clearOld" | 
    "configure" | "getConfiguration" | "configurationChanged" | "setState" | "activeCountChange" | "stateChange";

export type NotificationPermissionTypes = "default" | "granted" | "denied";

export const operations: { [key in NotificationsOperationTypes]: BridgeOperation } = {
    raiseNotification: { name: "raiseNotification", dataDecoder: raiseNotificationDecoder, resultDecoder:raiseNotificationResultDecoder },
    requestPermission: { name: "requestPermission", resultDecoder: permissionRequestResultDecoder },
    notificationShow: { name: "notificationShow", dataDecoder: notificationEventPayloadDecoder },
    notificationClick: { name: "notificationClick", dataDecoder: notificationEventPayloadDecoder },
    getPermission: { name: "getPermission", resultDecoder: permissionQueryResultDecoder },
    list: { name: "list", resultDecoder: allNotificationsDataDecoder },
    notificationRaised: { name: "notificationRaised", dataDecoder: simpleNotificationDataDecoder },
    notificationClosed: { name: "notificationClosed", dataDecoder: simpleNotificationSelectDecoder },
    click: { name: "click" },
    clear: { name: "clear" },
    clearAll: { name: "clearAll" },
    clearOld: { name: "clearOld" },
    configure: { name: "configure", dataDecoder: notificationsConfigurationProtocolDecoder },
    getConfiguration: { name: "getConfiguration", resultDecoder: strictNotificationsConfigurationProtocolDecoder },
    configurationChanged: { name: "configurationChanged", resultDecoder: strictNotificationsConfigurationProtocolDecoder },
    setState: { name: "setState", dataDecoder: notificationSetStateRequestDecoder },
    activeCountChange: { name: "activeCountChange", resultDecoder: activeNotificationsCountChangeDecoder },
    stateChange: { name: "stateChange", resultDecoder: notificationSetStateRequestDecoder }
};

export interface RaiseNotification {
    settings: IOConnectBrowser.Notifications.RaiseOptions;
    id: string;
}

export interface RaiseNotificationResult {
    settings: IOConnectBrowser.Notifications.RaiseOptions;
}

export interface PermissionRequestResult {
    permissionGranted: boolean;
}

export interface PermissionQueryResult {
    permission: NotificationPermissionTypes;
}

export interface NotificationEventPayload {
    definition: IOConnectBrowser.Notifications.NotificationDefinition;
    action?: string;
    id?: string;
}

export interface SimpleNotificationData {
    notification: IOConnectBrowser.Notifications.NotificationData;
}

export interface AllNotificationsData {
    notifications: IOConnectBrowser.Notifications.NotificationData[];
}

export interface NotificationClickConfig {
    id: string;
    action?: string;
}

export interface SimpleNotificationSelect {
    id: string;
}

export interface NotificationsConfigurationProtocol {
    configuration: IOConnectBrowser.Notifications.Configuration;
}

export interface StrictSourceFiler extends IOConnectBrowser.Notifications.NotificationFilter {
    allowed: NonNullable<IOConnectBrowser.Notifications.NotificationFilter["allowed"]>;
    blocked: NonNullable<IOConnectBrowser.Notifications.NotificationFilter["blocked"]>;
}

export interface StrictConfiguration extends IOConnectBrowser.Notifications.Configuration {
    enable: NonNullable<IOConnectBrowser.Notifications.Configuration["enable"]>;
    enableToasts: NonNullable<IOConnectBrowser.Notifications.Configuration["enableToasts"]>;
    sourceFilter: StrictSourceFiler;
}

export interface StrictNotificationsConfigurationProtocol {
    configuration: StrictConfiguration;
}

export interface NotificationSetStateRequest {
    id: string;
    state: IOConnectBrowser.Notifications.State;
}

export interface ActiveNotificationsCountChange {
    count: number;
}
