/* eslint-disable @typescript-eslint/no-explicit-any */
import { IOConnectCore } from "@interopio/core";
import { IoC } from "../shared/ioc";
import { LibController, OperationCheckConfig, OperationCheckResult } from "../shared/types";
import { IOConnectBrowser } from "../../browser";
import { GlueBridge } from "../communication/bridge";
import { UnsubscribeFunction } from "callback-registry";
import { intentsOperationTypesDecoder, raiseRequestDecoder, findFilterDecoder, AddIntentListenerDecoder, handlersFilterDecoder, intentHandlerDecoder } from "../shared/decoders";
import { operations, RaiseIntentArgs, WrappedIntentFilter, WrappedIntents } from "./protocol";
import { ADDITIONAL_BRIDGE_OPERATION_TIMEOUT, DEFAULT_PICK_HANDLER_BY_TIMEOUT, DEFAULT_RESOLVER_RESPONSE_TIMEOUT, GLUE42_FDC3_INTENTS_METHOD_PREFIX, INTENTS_RESOLVER_APP_NAME, MAX_SET_TIMEOUT_DELAY } from "./constants";
import { LegacyIntentsHelper } from "./legacyHelper";
import { systemOperations } from "../shared/systemOperations";
import { FilterHandlersWithResolverConfig, ResolverConfig, IntentRequestWithResolverConfig, IsRaiseOperationSupported, GetResolverConfigByRequestArgs } from "./types"; 

export class IntentsController implements LibController {
    private bridge!: GlueBridge;
    private logger!: IOConnectBrowser.Logger.API;
    private interop!: IOConnectCore.AGM.API;
    private legacyIntentsController!: LegacyIntentsHelper;
    private myIntents = new Set<string>();

    private useIntentsResolverUI = true;
    private intentsResolverAppName!: string;
    private intentResolverResponseTimeout!: number;

    private unregisterIntentPromises: Promise<void>[] = [];

    public async start(coreGlue: IOConnectCore.API, ioc: IoC): Promise<void> {
        this.logger = coreGlue.logger.subLogger("intents.controller.web");

        this.logger.trace("starting the web intents controller");

        this.bridge = ioc.bridge;

        this.interop = coreGlue.interop;

        this.legacyIntentsController = ioc.legacyIntentsHelper;

        this.checkIfIntentsResolverIsEnabled(ioc.config);

        const api = this.toApi();

        this.logger.trace("no need for platform registration, attaching the intents property to glue and returning");

        (coreGlue as IOConnectBrowser.API).intents = api;
    }

    public handlePlatformShutdown(): void {
        this.myIntents = new Set<string>();
        this.unregisterIntentPromises = [];
    }

    public async handleBridgeMessage(args: any): Promise<void> {
        const operationName = intentsOperationTypesDecoder.runWithException(args.operation);

        const operation = operations[operationName];

        if (!operation.execute) {
            return;
        }

        let operationData: any = args.data;

        if (operation.dataDecoder) {
            operationData = operation.dataDecoder.runWithException(args.data);
        }

        return await operation.execute(operationData);
    }

    private toApi(): IOConnectBrowser.Intents.API {
        const api: IOConnectBrowser.Intents.API = {
            raise: this.raise.bind(this),
            all: this.all.bind(this),
            addIntentListener: this.addIntentListener.bind(this),
            register: this.register.bind(this),
            find: this.find.bind(this),
            filterHandlers: this.filterHandlers.bind(this),
            getIntents: this.getIntentsByHandler.bind(this)
        };

        return api;
    }

    private async raise(request: string | IOConnectBrowser.Intents.IntentRequest): Promise<IOConnectBrowser.Intents.IntentResult> {
        const validatedIntentRequest = raiseRequestDecoder.runWithException(request);

        const intentRequest: IOConnectBrowser.Intents.IntentRequest = typeof validatedIntentRequest === "string"
            ? { intent: validatedIntentRequest }
            : validatedIntentRequest;

        await Promise.all(this.unregisterIntentPromises);

        const requestWithResolverInfo = { intentRequest, resolverConfig: this.getResolverConfigByRequest({ intentRequest }) };

        const isRaiseOperationSupported = await this.isRaiseOperationSupported();

        if (!isRaiseOperationSupported.supported) {
            this.logger.warn(`${isRaiseOperationSupported.reason}. Invoking legacy raise method`);

            return this.legacyIntentsController.raise(requestWithResolverInfo, this.find.bind(this));
        }

        this.logger.trace(`Sending raise request to the platform: ${JSON.stringify(request)} and method response timeout of ${this.intentResolverResponseTimeout}ms`);

        // waitUserResponseIndefinitely: true -> methodResponseTimeoutMs: Number.MAX_SAFE_INTEGER
        const methodResponseTimeoutMs = intentRequest.waitUserResponseIndefinitely 
            ? MAX_SET_TIMEOUT_DELAY
            : (intentRequest.timeout || this.intentResolverResponseTimeout) + ADDITIONAL_BRIDGE_OPERATION_TIMEOUT;

        const response = await this.bridge.send<IntentRequestWithResolverConfig, IOConnectBrowser.Intents.IntentResult>("intents", operations.raise, requestWithResolverInfo, { methodResponseTimeoutMs, waitTimeoutMs: methodResponseTimeoutMs });

        return response;
    }

    private getResolverConfigByRequest(filter: GetResolverConfigByRequestArgs): ResolverConfig {
        if (filter.handlerFilter) {
            return {
                enabled: typeof filter.handlerFilter?.openResolver === "boolean" ? filter.handlerFilter?.openResolver : this.useIntentsResolverUI,
                appName: this.intentsResolverAppName,
                waitResponseTimeout: filter.handlerFilter?.timeout || DEFAULT_PICK_HANDLER_BY_TIMEOUT
            };
        }


        // if waitUserResponseIndefinitely: true -> ignore resolver timeout and wait for eternity until the user chooses a handler
        const waitResponseTimeout = filter.intentRequest?.waitUserResponseIndefinitely ? MAX_SET_TIMEOUT_DELAY : this.intentResolverResponseTimeout;

        return { 
            enabled: this.useIntentsResolverUI,
            appName: this.intentsResolverAppName,
            waitResponseTimeout
        };
    }

    private async isRaiseOperationSupported(): Promise<IsRaiseOperationSupported> {
        try {
            const { isSupported } = await this.bridge.send<OperationCheckConfig, OperationCheckResult>("intents", systemOperations.operationCheck, { operation: "raise" });

            return {
                supported: isSupported,
                reason: isSupported ? "" : "The platform of this client is outdated and does not support \"raise\" operation"
            };
        } catch (error) {
            return {
                supported: false,
                reason: "The platform of this client is outdated and does not support \"operationCheck\" command"
            };
        }
    }

    private async all(): Promise<IOConnectBrowser.Intents.Intent[]> {
        await Promise.all(this.unregisterIntentPromises);

        const result = await this.bridge.send<void, WrappedIntents>("intents", operations.getIntents, undefined);

        return result.intents;
    }

    private addIntentListener(intent: string | IOConnectBrowser.Intents.AddIntentListenerRequest, handler: (context: IOConnectBrowser.Intents.IntentContext) => any): { unsubscribe: UnsubscribeFunction } {
        AddIntentListenerDecoder.runWithException(intent);
        if (typeof handler !== "function") {
            throw new Error("Cannot add intent listener, because the provided handler is not a function!");
        }

        // eslint-disable-next-line prefer-const
        let registerPromise: Promise<void>;

        // `addIntentListener()` is sync.
        const intentName = typeof intent === "string" ? intent : intent.intent;
        const methodName = this.buildInteropMethodName(intentName);

        const alreadyRegistered = this.myIntents.has(intentName);

        if (alreadyRegistered) {
            throw new Error(`Intent listener for intent ${intentName} already registered!`);
        }
        this.myIntents.add(intentName);

        const result = {
            unsubscribe: (): void => {
                this.myIntents.delete(intentName);

                registerPromise
                    .then(() => this.interop.unregister(methodName))
                    .catch((err) => this.logger.trace(`Unregistration of a method with name ${methodName} failed with reason: ${err}`));
            }
        };

        let intentFlag: Omit<IOConnectBrowser.Intents.AddIntentListenerRequest, "intent"> = {};

        if (typeof intent === "object") {
            // eslint-disable-next-line @typescript-eslint/no-unused-vars
            const { intent: removed, ...rest } = intent;
            intentFlag = rest;
        }

        registerPromise = this.interop.register({ name: methodName, flags: { intent: intentFlag } }, (args: RaiseIntentArgs) => {
            if (this.myIntents.has(intentName)) {
                // eslint-disable-next-line @typescript-eslint/no-unused-vars
                const { _initialCallerId, ...rest } = args;

                return handler(rest);
            }
        });

        registerPromise.catch(err => {
            this.myIntents.delete(intentName);

            this.logger.warn(`Registration of a method with name ${methodName} failed with reason: ${err}`);
        });

        return result;
    }

    private async register(intent: string | IOConnectBrowser.Intents.AddIntentListenerRequest, handler: (context: IOConnectBrowser.Intents.IntentContext, caller?: IOConnectCore.Interop.Instance) => any): Promise<{ unsubscribe: UnsubscribeFunction }> {
        AddIntentListenerDecoder.runWithException(intent);

        if (typeof handler !== "function") {
            throw new Error("Cannot add intent listener, because the provided handler is not a function!");
        }

        await Promise.all(this.unregisterIntentPromises);

        const intentName = typeof intent === "string" ? intent : intent.intent;
        const methodName = this.buildInteropMethodName(intentName);

        const alreadyRegistered = this.myIntents.has(intentName);

        if (alreadyRegistered) {
            throw new Error(`Intent listener for intent ${intentName} already registered!`);
        }
        this.myIntents.add(intentName);

        let intentFlag: Omit<IOConnectBrowser.Intents.AddIntentListenerRequest, "intent"> = {};

        if (typeof intent === "object") {
            // eslint-disable-next-line @typescript-eslint/no-unused-vars
            const { intent: removed, ...rest } = intent;
            intentFlag = rest;
        }

        try {
            await this.interop.register({ name: methodName, flags: { intent: intentFlag } }, (args: RaiseIntentArgs) => {
                if (this.myIntents.has(intentName)) {
                    const { _initialCallerId, ...rest } = args;

                    const caller = this.interop.servers().find((server) => server.instance === _initialCallerId);

                    return handler(rest, caller);
                }
            });
        } catch (err) {
            this.myIntents.delete(intentName);

            throw new Error(`Registration of a method with name ${methodName} failed with reason: ${JSON.stringify(err)}`);
        }

        return {
            unsubscribe: () => this.unsubscribeIntent(intentName)
        };
    }

    private async find(intentFilter?: string | IOConnectBrowser.Intents.IntentFilter): Promise<IOConnectBrowser.Intents.Intent[]> {
        let data: WrappedIntentFilter | undefined = undefined;

        if (typeof intentFilter !== "undefined") {
            const intentFilterObj = findFilterDecoder.runWithException(intentFilter);

            if (typeof intentFilterObj === "string") {
                data = {
                    filter: {
                        name: intentFilterObj
                    }
                };
            } else if (typeof intentFilterObj === "object") {
                data = {
                    filter: intentFilterObj
                };
            }
        }

        await Promise.all(this.unregisterIntentPromises);

        const result = await this.bridge.send<WrappedIntentFilter | undefined, WrappedIntents>("intents", operations.findIntent, data);

        return result.intents;
    }

    private checkIfIntentsResolverIsEnabled(options: IOConnectBrowser.Config): void {
        this.useIntentsResolverUI = typeof options.intents?.enableIntentsResolverUI === "boolean"
            ? options.intents.enableIntentsResolverUI
            : true;

        this.intentsResolverAppName = options.intents?.intentsResolverAppName ?? INTENTS_RESOLVER_APP_NAME;

        this.intentResolverResponseTimeout = options.intents?.methodResponseTimeoutMs ?? DEFAULT_RESOLVER_RESPONSE_TIMEOUT;
    }

    private clearUnregistrationPromise(promiseToRemove: Promise<void>): void {
        this.unregisterIntentPromises = this.unregisterIntentPromises.filter(promise => promise !== promiseToRemove);
    }

    private buildInteropMethodName(intentName: string): string {
        return `${GLUE42_FDC3_INTENTS_METHOD_PREFIX}${intentName}`;
    }

    private unsubscribeIntent(intentName: string): void {
        this.myIntents.delete(intentName);

        const methodName = this.buildInteropMethodName(intentName);

        // typings are wrong and mark unregister as a sync method
        const unregisterPromise = this.interop.unregister(methodName) as unknown as Promise<void>;

        this.unregisterIntentPromises.push(unregisterPromise);

        unregisterPromise
            .then(() => {
                this.clearUnregistrationPromise(unregisterPromise);
            })
            .catch((err) => {
                this.logger.error(`Unregistration of a method with name ${methodName} failed with reason: ${err}`);

                this.clearUnregistrationPromise(unregisterPromise);
            });
    }

    private async filterHandlers(handlerFilter: IOConnectBrowser.Intents.HandlerFilter): Promise<IOConnectBrowser.Intents.FilterHandlersResult> {
        handlersFilterDecoder.runWithException(handlerFilter);
    
        this.checkIfAtLeastOneFilterIsPresent(handlerFilter);

        if (handlerFilter.openResolver && !this.useIntentsResolverUI) {
            throw new Error("Cannot resolve 'filterHandlers' request using Intents Resolver UI because it's globally disabled");
        }

        const methodResponseTimeoutMs = (handlerFilter.timeout || DEFAULT_PICK_HANDLER_BY_TIMEOUT) + ADDITIONAL_BRIDGE_OPERATION_TIMEOUT;

        const filterHandlersRequestWithResolverConfig: FilterHandlersWithResolverConfig = { filterHandlersRequest: handlerFilter, resolverConfig: this.getResolverConfigByRequest({ handlerFilter }) };
        
        const result = await this.bridge.send<FilterHandlersWithResolverConfig, IOConnectBrowser.Intents.FilterHandlersResult>("intents", operations.filterHandlers, filterHandlersRequestWithResolverConfig, { methodResponseTimeoutMs, waitTimeoutMs: methodResponseTimeoutMs }, { includeOperationCheck: true });

        return result;
    }

    private checkIfAtLeastOneFilterIsPresent(filter: IOConnectBrowser.Intents.HandlerFilter): void {
        const errorMsg = "Provide at least one filter criteria of the following: 'intent' | 'contextTypes' | 'resultType' | 'applicationNames'";
        
        if (!Object.keys(filter).length) {
            // only title was present
            throw new Error(errorMsg);
        }

        const { intent, resultType, contextTypes, applicationNames } = filter;

        const existingValidContextTypes = contextTypes?.length;
        const existingValidApplicationNames = applicationNames?.length;

        if (!intent && !resultType && !existingValidContextTypes && !existingValidApplicationNames) {
            throw new Error(errorMsg);
        }
    }

    private async getIntentsByHandler(handler: IOConnectBrowser.Intents.IntentHandler): Promise<IOConnectBrowser.Intents.GetIntentsResult> {
        intentHandlerDecoder.runWithException(handler);

        const result = await this.bridge.send<IOConnectBrowser.Intents.IntentHandler, IOConnectBrowser.Intents.GetIntentsResult>("intents", operations.getIntentsByHandler, handler, undefined, { includeOperationCheck: true });

        return result;
    }
}
