import Utils from "../utils";
import { IOConnectDesktop } from "../../desktop";
import { AppDefinition, CoreRaiseIntentArgs, CreateResponsePromiseArgs, FilterHandlersResolverResponse, GetWindowsInfoResult, GetWindowsInfoResultItem, IntentInfo, IntentsResolverResponse, IntentsResolverResponsePromise, IntentsResolverResponseValidator, IntentsResolverStartContext, RaiseResolverResponse, ResolverInstance, ShouldResolverOpen, StartResolverAppArgs, StartResolverAppMethod } from "./types";
import { INTENTS_RESOLVER_INTEROP_PREFIX, GLUE42_FDC3_INTENTS_METHOD_PREFIX, INTENTS_RESOLVER_HEIGHT, INTENTS_RESOLVER_WIDTH, INTENT_HANDLER_DEFAULT_PROPS, INTENTS_RESOLVER_APP_NAME, DEFAULT_RESOLVER_RESPONSE_TIMEOUT, DEFAULT_RAISE_TIMEOUT_MS, DEFAULT_METHOD_RESPONSE_TIMEOUT_MS, MAX_SET_TIMEOUT_DELAY, DEFAULT_PICK_HANDLER_BY_TIMEOUT_MS } from "./constants";
import { PromisePlus, PromiseWrap } from "./common";
import { clearNullUndefined, validateHandlerFilter, validateIntentHandler, validateIntentRequest, validateIntentsResolverResponse } from "./helpers";

export class Intents implements IOConnectDesktop.Intents.API {
    private myIntents = new Set<string>();

    private intentsResolverAppName: string;
    private readonly intentsResolverResponsePromises: { [instanceId: string]: IntentsResolverResponsePromise } = {};

    private useIntentsResolverUI = true;
    private intentsResolverResponseTimeout: number;

    private unregisterIntentPromises: Promise<void>[] = [];

    constructor(private interop: IOConnectDesktop.Interop.API, private windows: IOConnectDesktop.Windows.API, private logger: IOConnectDesktop.Logger.API, options: IOConnectDesktop.Config, private appManager?: IOConnectDesktop.AppManager.API) {
        this.checkIfIntentsResolverIsEnabled(options, appManager);
    }

    public async find(intentFilter?: string | IOConnectDesktop.Intents.IntentFilter): Promise<IOConnectDesktop.Intents.Intent[]> {
        await Promise.all(this.unregisterIntentPromises);

        let intents = await this.all();
        if (typeof intentFilter === "undefined") {
            return intents;
        }

        if (typeof intentFilter === "string") {
            return intents.filter((intent) => intent.name === intentFilter);
        }

        if (typeof intentFilter !== "object") {
            throw new Error("Please provide the intentFilter as a string or an object!");
        }

        if (intentFilter.contextType) {
            const ctToLower = intentFilter.contextType.toLowerCase();
            intents = intents.filter((intent) => intent.handlers.some((handler) => handler.contextTypes?.some((ct) => ct.toLowerCase() === ctToLower)));
        }

        if (intentFilter.resultType) {
            const resultTypeToLower = intentFilter.resultType.toLowerCase();
            intents = intents.filter((intent) => intent.handlers.some((handler) => handler.resultType?.toLowerCase() === resultTypeToLower));
        }

        if (intentFilter.name) {
            intents = intents.filter((intent) => intent.name === intentFilter.name);
        }

        return intents;
    }

    public async raise(intentRequest: string | IOConnectDesktop.Intents.IntentRequest): Promise<IOConnectDesktop.Intents.IntentResult> {
        if ((typeof intentRequest !== "string" && typeof intentRequest !== "object") || (typeof intentRequest === "object" && typeof intentRequest.intent !== "string")) {
            throw new Error("Please provide the intent as a string or an object with an intent property!");
        }

        if (typeof intentRequest === "string") {
            intentRequest = { intent: intentRequest };
        }

        validateIntentRequest(intentRequest);

        await Promise.all(this.unregisterIntentPromises);

        const timeout = intentRequest.timeout || DEFAULT_RAISE_TIMEOUT_MS;

        const resolverInstance: ResolverInstance = {};

        const coreRaiseIntentFn = this.coreRaiseIntent.bind(this, { request: intentRequest, resolverInstance, timeout });

        if (intentRequest.waitUserResponseIndefinitely) {
            return coreRaiseIntentFn();
        }

        // start timeout here -> default (waitUserResponseIndefinitely: false)
        const resultPromise = PromiseWrap<IOConnectDesktop.Intents.IntentResult>(coreRaiseIntentFn,
            timeout, `Timeout of ${timeout}ms hit for intent request ${JSON.stringify(intentRequest)}`
        );

        resultPromise.catch(() => this.handleRaiseOnError(resolverInstance.instanceId));

        return resultPromise;
    }

    public async all(): Promise<IOConnectDesktop.Intents.Intent[]> {
        await Promise.all(this.unregisterIntentPromises);

        // Gathers all intents from:
        // 1. Application definitions
        // 2. Running instances (application can register dynamic intents by calling `addIntentListener()` that aren't predefined inside of their application definitions)
        // It also populates intent handlers (actual entities that can handle the intent).
        let apps: AppDefinition[];
        try {
            const result = await this.interop.invoke<{ applications: AppDefinition[] }>("T42.ACS.GetApplications", { withIntentsInfo: true });
            apps = result.returned.applications;
        } catch (e) {
            this.logger.error(`Failed to get the applications!`, e);
            return [];
        }
        const intents: { [key: string]: IOConnectDesktop.Intents.Intent } = {};
        const appsWithIntents = apps.filter((app) => app.intents && app.intents.length > 0);
        //  Gather app handlers from application definitions.
        for (const app of appsWithIntents) {
            for (const intentDef of app.intents) {
                let intent = intents[intentDef.name];
                if (!intent) {
                    intent = {
                        name: intentDef.name,
                        handlers: [],
                    };
                    intents[intentDef.name] = intent;
                }

                const handler: IOConnectDesktop.Intents.IntentHandler = {
                    applicationName: app.name,
                    applicationTitle: app.title || "",
                    applicationDescription: app.caption,
                    displayName: intentDef.displayName,
                    contextTypes: intentDef.contexts,
                    applicationIcon: app.icon,
                    type: "app",
                    resultType: intentDef.resultType
                };

                intent.handlers.push(handler);
            }
        }

        const servers = this.interop.servers();
        const serverWindowIds = servers.map((server) => server.windowId).filter((serverWindowId) => typeof serverWindowId !== "undefined");

        const T42WndGetInfo = "T42.Wnd.GetInfo";
        const isT42WndGetInfoMethodRegistered = this.interop.methods().some((method) => method.name === T42WndGetInfo);

        let windowsInfos: GetWindowsInfoResultItem[];
        // For backwards compatibility with previous versions of Glue Desktop, the windows' titles will be attempted to be obtained using the Windows API.
        if (isT42WndGetInfoMethodRegistered) {
            try {
                const result = await this.interop.invoke<GetWindowsInfoResult>(T42WndGetInfo, { ids: serverWindowIds });
                windowsInfos = result.returned.windows;
            } catch (e) {
                // Swallow the error. The windows' titles will be attempted to be obtained using the Windows API.
            }
        }

        // Discover all running instances that provide intents, and add them to the corresponding intent.
        for (const server of servers) {
            await Promise.all(server.getMethods()
                .filter((method) => method.name.startsWith(GLUE42_FDC3_INTENTS_METHOD_PREFIX))
                .map(async (method) => {
                    const intentName = method.name.replace(GLUE42_FDC3_INTENTS_METHOD_PREFIX, "");
                    let intent = intents[intentName];
                    if (!intent) {
                        intent = {
                            name: intentName,
                            handlers: [],
                        };
                        intents[intentName] = intent;
                    }
                    const info = method.flags.intent as Omit<IOConnectDesktop.Intents.AddIntentListenerRequest, "intent">;

                    const app = apps.find((appWithIntents) => appWithIntents.name === server.application);
                    let appIntent: IntentInfo | undefined;
                    if (app && app.intents) {
                        appIntent = app.intents.find((appDefIntent) => appDefIntent.name === intentName);
                    }

                    const title = await this.windowsIdToTitle(server.windowId, windowsInfos);
                    const handler: IOConnectDesktop.Intents.IntentHandler = {
                        instanceId: server.instance,
                        applicationName: server.application,
                        applicationIcon: info.icon || app?.icon,
                        applicationTitle: app?.title || "",
                        applicationDescription: info.description || app?.caption,
                        displayName: info.displayName || appIntent?.displayName,
                        contextTypes: info.contextTypes || appIntent?.contexts,
                        instanceTitle: title,
                        type: "instance",
                        resultType: appIntent?.resultType || info.resultType
                    };
                    intent.handlers.push(handler);
                }));
        }

        return Object.values(intents);
    }

    public addIntentListener(intent: string | IOConnectDesktop.Intents.AddIntentListenerRequest, handler: (context: IOConnectDesktop.Intents.IntentContext) => any): { unsubscribe: () => void } {
        if ((typeof intent !== "string" && typeof intent !== "object") || (typeof intent === "object" && typeof intent.intent !== "string")) {
            throw new Error("Please provide the intent as a string or an object with an intent property!");
        }
        if (typeof handler !== "function") {
            throw new Error("Please provide the handler as a function!");
        }

        // `addIntentListener()` is sync.
        // tslint:disable-next-line:no-console
        const intentName = typeof intent === "string" ? intent : intent.intent;
        const methodName = `${GLUE42_FDC3_INTENTS_METHOD_PREFIX}${intentName}`;
        let intentFlag: Omit<IOConnectDesktop.Intents.AddIntentListenerRequest, "intent"> = {};
        // eslint-disable-next-line prefer-const
        let registerPromise: Promise<void>;

        const alreadyRegistered = this.myIntents.has(intentName);

        if (alreadyRegistered) {
            throw new Error(`Intent listener for intent ${intentName} already registered!`);
        }

        this.myIntents.add(intentName);

        const result = {
            unsubscribe: (): void => {
                this.myIntents.delete(intentName);

                registerPromise
                    .then(() => this.interop.unregister(methodName))
                    .catch((err) => this.logger.trace(`Unregistration of a method with name ${methodName} failed with reason: ${JSON.stringify(err)}`));
            }
        };

        if (typeof intent === "object") {
            const { intent: removed, ...rest } = intent;
            intentFlag = rest;
        }

        registerPromise = this.interop.register({ name: methodName, flags: { intent: intentFlag } }, (args: IOConnectDesktop.Intents.IntentContext) => {
            if (this.myIntents.has(intentName)) {
                return handler(args);
            }
        });

        registerPromise.catch((err) => {
            this.myIntents.delete(intentName);

            this.logger.warn(`Registration of a method with name ${methodName} failed with reason: ${JSON.stringify(err)}`);
        });

        return result;
    }

    public async register(intent: string | IOConnectDesktop.Intents.AddIntentListenerRequest, handler: (context: IOConnectDesktop.Intents.IntentContext, caller?: IOConnectDesktop.Interop.Instance) => any): Promise<{ unsubscribe: () => void }> {
        if ((typeof intent !== "string" && typeof intent !== "object") || (typeof intent === "object" && typeof intent.intent !== "string")) {
            throw new Error("Please provide the intent as a string or an object with an intent property!");
        }

        if (typeof handler !== "function") {
            throw new Error("Please provide the handler as a function!");
        }

        await Promise.all(this.unregisterIntentPromises);

        // tslint:disable-next-line:no-console
        const intentName = typeof intent === "string" ? intent : intent.intent;
        const methodName = this.buildInteropMethodName(intentName);

        let intentFlag: Omit<IOConnectDesktop.Intents.AddIntentListenerRequest, "intent"> = {};

        const alreadyRegistered = this.myIntents.has(intentName);

        if (alreadyRegistered) {
            throw new Error(`Intent listener for intent ${intentName} already registered!`);
        }

        this.myIntents.add(intentName);

        if (typeof intent === "object") {
            const { intent: removed, ...rest } = intent;
            intentFlag = rest;
        }

        try {
            await this.interop.register({ name: methodName, flags: { intent: intentFlag } }, (args: IOConnectDesktop.Intents.IntentContext, caller: IOConnectDesktop.Interop.Instance) => {
                if (this.myIntents.has(intentName)) {
                    return handler(args, caller);
                }
            });
        } catch (err) {
            this.myIntents.delete(intentName);

            throw new Error(`Registration of a method with name ${methodName} failed with reason: ${JSON.stringify(err)}`);
        }

        return {
            unsubscribe: () => this.unsubscribeIntent(intentName)
        };
    }

    public async filterHandlers(handlerFilter: IOConnectDesktop.Intents.HandlerFilter): Promise<IOConnectDesktop.Intents.FilterHandlersResult> {
        validateHandlerFilter(handlerFilter);

        if (handlerFilter.openResolver && !this.useIntentsResolverUI) {
            throw new Error("Cannot resolve 'filterHandlers' request using Intents Resolver UI because it's globally disabled");
        }

        this.logger?.trace(`Received 'filterHandlers' command with request: ${JSON.stringify(handlerFilter)}`);

        const filteredHandlers = this.filterHandlersBy(await this.all(), handlerFilter);

        if (!filteredHandlers || !filteredHandlers.length) {
            return { handlers: [] };
        }

        const { open, reason } = this.checkIfResolverShouldBeOpenedForFilterHandlers(filteredHandlers, handlerFilter);

        if (!open) {
            this.logger?.trace(`Intent Resolver UI won't be used. Reason: ${reason}`);

            return { handlers: filteredHandlers };
        }

        const resolverInstance: ResolverInstance = { instanceId: undefined };

        const timeout = handlerFilter.timeout || DEFAULT_PICK_HANDLER_BY_TIMEOUT_MS;

        const handler = await PromiseWrap(() => this.startResolverApp({ request: handlerFilter, resolverInstance, method: 'filterHandlers' }),
            timeout,
            `Timeout of ${timeout}ms hit for 'filterHandlers' request with filter: ${JSON.stringify(handlerFilter)}`
        );

        return { handlers: [handler] };
    }

    public async getIntents(handler: IOConnectDesktop.Intents.IntentHandler): Promise<IOConnectDesktop.Intents.GetIntentsResult> {
        this.logger.trace(`Received 'getIntents' command with handler ${JSON.stringify(handler)}`);

        validateIntentHandler(handler);

        const intents = await this.all();

        clearNullUndefined(handler);

        this.logger?.trace(`Extracting valid intents for the passed handler`);

        const intentsWithInfo = this.extractIntentsWithInfoByHandler(intents, handler);

        this.logger.trace(`Returning intents for handler ${JSON.stringify(handler)}`);

        return { intents: intentsWithInfo };
    }

    private filterHandlersBy(intents: IOConnectDesktop.Intents.Intent[], filter: IOConnectDesktop.Intents.HandlerFilter): IOConnectDesktop.Intents.IntentHandler[] {
        const filteredIntentsWithHandlers = intents.filter((intent: IOConnectDesktop.Intents.Intent) => {
            if (filter.intent && filter.intent !== intent.name) {
                return;
            }

            if (filter.resultType) {
                const filteredHandlers = intent.handlers.filter((handler: IOConnectDesktop.Intents.IntentHandler) => handler.resultType && handler.resultType === filter.resultType);

                if (!filteredHandlers.length) return;

                intent.handlers = filteredHandlers;
            }

            if (filter.contextTypes) {
                const filteredHandlers = intent.handlers.filter((handler: IOConnectDesktop.Intents.IntentHandler) => filter.contextTypes?.every((contextType: string) => handler.contextTypes?.includes(contextType)));

                if (!filteredHandlers.length) return;

                intent.handlers = filteredHandlers;
            }

            if (filter.applicationNames) {
                const filteredHandlers = intent.handlers.filter((handler: IOConnectDesktop.Intents.IntentHandler) => filter.applicationNames?.includes(handler.applicationName));

                if (!filteredHandlers.length) return;

                intent.handlers = filteredHandlers;
            }

            return intent;
        });

        return filteredIntentsWithHandlers.map((intent) => intent.handlers).flat(1);
    }

    private async coreRaiseIntent({ request, resolverInstance, timeout }: CoreRaiseIntentArgs): Promise<IOConnectDesktop.Intents.IntentResult> {
        const intentDef = await this.get(request.intent);

        if (typeof intentDef === "undefined") {
            throw new Error(`Intent ${request.intent} not found.`);
        }

        const { open, reason } = this.checkIfResolverShouldBeOpenedForRaise(intentDef, request);

        if (!open) {
            this.logger.trace(`Intent Resolver UI won't be used. Reason: ${reason}`);

            // start timeout here -> waitUserResponseIndefinitely: true, but resolver will not be opened
            return request.waitUserResponseIndefinitely
                ? PromiseWrap<IOConnectDesktop.Intents.IntentResult>(() => this.raiseIntent(request, timeout), timeout, `Timeout of ${timeout}ms hit for raise to resolve`)
                : this.raiseIntent(request, timeout);
        }

        const resolverHandler = await this.startResolverApp({ request, method: "raise", resolverInstance});

        this.logger?.trace(`Raising intent to target handler: ${JSON.stringify(resolverHandler)} with timeout of ${timeout}`);

        if (request.waitUserResponseIndefinitely) {
            return PromiseWrap(() => this.raiseIntentToTargetHandler(request, resolverHandler, timeout), timeout, `Timeout of ${timeout}ms hit for raise to resolve`);
        }

        const result = await this.raiseIntentToTargetHandler(request, resolverHandler, timeout);

        this.logger?.trace(`Result from raise() method for intent ${JSON.stringify(request.intent)}: ${JSON.stringify(result)}`);

        return result;
    }

    private async get(intent: string): Promise<IOConnectDesktop.Intents.Intent> {
        return (await this.all()).find((registeredIntent) => registeredIntent.name === intent);
    }

    private async raiseIntent(intentRequest: IOConnectDesktop.Intents.IntentRequest, timeout?: number): Promise<IOConnectDesktop.Intents.IntentResult> {
        const intentName = intentRequest.intent;
        const intentDef = await this.get(intentName);

        if (typeof intentDef === "undefined") {
            throw new Error(`Intent ${intentName} not found.`);
        }

        const firstFoundAppHandler = intentRequest.handlers ? this.findHandlerByFilter(intentRequest.handlers, { type: "app" }) : this.findHandlerByFilter(intentDef.handlers, { type: "app" });

        const firstFoundInstanceHandler = intentRequest.handlers ? this.findHandlerByFilter(intentRequest.handlers, { type: "instance" }) : this.findHandlerByFilter(intentDef.handlers, { type: "instance" });

        let handler: IOConnectDesktop.Intents.IntentHandler | undefined;

        // target the first found instance or start a new one if there isn't
        if (!intentRequest.target || intentRequest.target === "reuse") {
            handler = firstFoundInstanceHandler || firstFoundAppHandler;
        }

        // start new instance of the first handler in handlers / intentDef.handlers
        if (intentRequest.target === "startNew") {
            handler = firstFoundAppHandler;
        }

        // open a new app from the list of intentRequest.handlers (if passed) or intentDef.handlers
        if (typeof intentRequest.target === "object" && intentRequest.target.app) {
            handler = this.findHandlerByFilter(intentDef.handlers, { app: intentRequest.target.app });
        }

        // target the instance in the list of intentRequest.handlers (if passed) or intentDef.handlers
        if (typeof intentRequest.target === "object" && intentRequest.target.instance) {
            handler = this.findHandlerByFilter(intentDef.handlers, { instance: intentRequest.target.instance, app: intentRequest.target.app });
        }

        if (!handler) {
            throw new Error(`Can not raise intent for request ${JSON.stringify(intentRequest)} - can not find intent handler!`);
        }

        const result = await this.raiseIntentToTargetHandler(intentRequest, handler, timeout);

        return result;
    }

    private async raiseIntentToTargetHandler(intentRequest: IOConnectDesktop.Intents.IntentRequest, handler: IOConnectDesktop.Intents.IntentHandler, timeout?: number): Promise<IOConnectDesktop.Intents.IntentResult> {
        this.logger?.trace(`Raising intent to target handler:${JSON.stringify(handler)}`);

        if (!handler.instanceId) {
            handler.instanceId = await this.invokeStartApp(handler.applicationName, intentRequest.context, intentRequest.options);
        }

        const methodName = `${GLUE42_FDC3_INTENTS_METHOD_PREFIX}${intentRequest.intent}`;

        const invokeOptions = {
            methodResponseTimeoutMs: timeout ? timeout + 1000 : DEFAULT_METHOD_RESPONSE_TIMEOUT_MS,
            waitTimeoutMs: timeout ? timeout + 1000 : DEFAULT_METHOD_RESPONSE_TIMEOUT_MS
        };

        const result = await this.interop.invoke(methodName, intentRequest.context, { instance: handler.instanceId }, invokeOptions);

        this.logger?.trace(`raiseIntent command completed. Returning result: ${JSON.stringify(result)}`);

        return {
            request: intentRequest,
            handler: { ...handler, type: "instance" },
            result: result.returned
        };
    }

    private async startResolverApp({ request, method, resolverInstance }: StartResolverAppArgs): Promise<IOConnectDesktop.Intents.IntentHandler> {
        this.logger?.trace(`Intents Resolver UI with app name ${this.intentsResolverAppName} will be used for request: ${JSON.stringify(request)}`);

        const responseMethodName = await this.registerIntentResolverMethod(method);

        this.logger?.trace(`Registered interop method ${responseMethodName}`);

        const startContext = this.buildStartContext(method, request, responseMethodName);

        const startOptions = await this.buildStartOptions();

        this.logger?.trace(`Starting Intents Resolver UI with context: ${JSON.stringify(startContext)} and options: ${JSON.stringify(startOptions)}`);

        const instance = await this.appManager.application(this.intentsResolverAppName).start(startContext, startOptions);

        resolverInstance.instanceId = instance.id;

        this.logger?.trace(`Intents Resolver instance with id ${instance.id} opened`);

        this.subscribeOnInstanceStopped(instance, method);

        const timeout = request.timeout || method === "raise" ? DEFAULT_RAISE_TIMEOUT_MS : DEFAULT_PICK_HANDLER_BY_TIMEOUT_MS;

        this.createResponsePromise({
            intent: method === "raise" ? (request as IOConnectDesktop.Intents.IntentRequest).intent : undefined,
            instanceId: instance.id,
            responseMethodName,
            timeout,
            errorMsg: `Timeout of ${timeout}ms hit waiting for the user to choose a handler ${method === "raise"
                ? `for intent ${(request as IOConnectDesktop.Intents.IntentRequest).intent}`
                : `for '${method}' method with filter ${JSON.stringify((request as IOConnectDesktop.Intents.HandlerFilter))}`
            }`
        });

        const handler = await this.handleInstanceResponse(instance.id, method);

        return handler;
    }

    private async windowsIdToTitle(id: string, windowsInfos?: GetWindowsInfoResultItem[]): Promise<string> {
        if (typeof windowsInfos !== "undefined") {
            return windowsInfos.find((windowsInfo) => windowsInfo.id === id)?.title;
        }

        // The Windows API could be undefined e.g. when running inside of a node environment.
        const window = this.windows?.findById(id);
        const title = await window?.getTitle();
        return title;
    }

    private async handleInstanceResponse(instanceId: string, method: StartResolverAppMethod): Promise<IOConnectDesktop.Intents.IntentHandler> {
        try {
            const response = await this.intentsResolverResponsePromises[instanceId].promise;

            this.logger?.trace(`Intent handler chosen ${method === "raise" ? `for intent ${(response as RaiseResolverResponse).intent} ` : "" }: ${JSON.stringify(response.handler)}. Stopping resolver instance with id ${instanceId}`);

            this.stopResolverInstance(instanceId);

            this.logger?.trace(`Instance with id ${instanceId} successfully stopped`);

            return response.handler;

        } catch (error) {
            this.stopResolverInstance(instanceId);

            throw new Error(error as string);
        }
    }

    private async registerIntentResolverMethod(method: StartResolverAppMethod): Promise<string> {
        const methodName = INTENTS_RESOLVER_INTEROP_PREFIX + Utils.generateId();

        await this.interop.register(methodName, (args, callerId) => this.resolverResponseHandler(args, callerId, method));

        return methodName;
    }

    private resolverResponseHandler(args: any, callerId: IOConnectDesktop.Interop.Instance, method: StartResolverAppMethod): void {
        const { instance } = callerId;

        const isValid = validateIntentsResolverResponse(method, args);

        if (!isValid) {
            this.logger.trace(`Intent Resolver instance with id ${callerId.instance} sent invalid response. Error: ${isValid.error}`);

            this.intentsResolverResponsePromises[instance].reject(isValid.error);

            this.stopResolverInstance(instance);

            return;
        }

        const validResponse = isValid.ok;

        this.intentsResolverResponsePromises[instance].resolve(validResponse);

        this.cleanUpIntentResolverPromise(instance);
    }

    private buildStartContext(method: StartResolverAppMethod, request: IOConnectDesktop.Intents.IntentRequest | IOConnectDesktop.Intents.HandlerFilter, methodName: string): IntentsResolverStartContext {
        const baseStartContext = { callerId: this.interop.instance.instance, methodName };

        return method === "raise"
            ? { ...baseStartContext, intent: request as IOConnectDesktop.Intents.IntentRequest }
            : { ...baseStartContext, handlerFilter: request as IOConnectDesktop.Intents.HandlerFilter };
    }

    private async buildStartOptions(): Promise<IOConnectDesktop.AppManager.ApplicationStartOptions | undefined> {
        const win = this.windows.my();

        if (!win) {
            return;
        }

        const bounds = await win.getBounds();

        return {
            top: (bounds.height - INTENTS_RESOLVER_HEIGHT) / 2 + bounds.top,
            left: (bounds.width - INTENTS_RESOLVER_WIDTH) / 2 + bounds.left,
            width: INTENTS_RESOLVER_WIDTH,
            height: INTENTS_RESOLVER_HEIGHT
        };
    }

    private createResponsePromise({ instanceId, intent, responseMethodName, timeout, errorMsg }: CreateResponsePromiseArgs): void {
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        let resolve: (arg: RaiseResolverResponse | FilterHandlersResolverResponse ) => void = () => { };
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        let reject: (reason: string) => void = () => { };

        const promise = PromisePlus<RaiseResolverResponse | FilterHandlersResolverResponse>((res, rej) => {
            resolve = res;
            reject = rej;
        }, timeout, errorMsg);

        this.intentsResolverResponsePromises[instanceId] = { intent, resolve, reject, promise, methodName: responseMethodName };
    }

    private async invokeStartApp(application: string, context: any, options?: IOConnectDesktop.AppManager.ApplicationStartOptions) {
        const result = await this.interop.invoke<{ Name: string, Id: string }>("T42.ACS.StartApplication", { Name: application, options });
        return result.returned.Id;
    }

    private subscribeOnInstanceStopped(instance: IOConnectDesktop.AppManager.Instance, method: StartResolverAppMethod): void {
        const { application } = instance;

        const unsub = application.onInstanceStopped((inst: IOConnectDesktop.AppManager.Instance) => {
            if (inst.id !== instance.id) {
                return;
            }

            const intentPromise = this.intentsResolverResponsePromises[inst.id];

            if (!intentPromise) {
                return unsub();
            }

            const errorMsg = `Cannot resolve ${method === "raise" ? `raised intent ${intentPromise.intent}` : `'${method}' method` } - User closed ${instance.application.name} app without choosing a handler`;

            intentPromise.reject(errorMsg);

            this.cleanUpIntentResolverPromise(inst.id);

            unsub();
        });
    }

    private async cleanUpIntentResolverPromise(instanceId: string) {
        const intentPromise = this.intentsResolverResponsePromises[instanceId];

        if (!intentPromise) {
            return;
        }

        // typings are wrong and mark unregister as a sync method
        const unregisterPromise = this.interop.unregister(intentPromise.methodName) as unknown as Promise<void>;

        unregisterPromise.catch((error) => this.logger.warn(error));

        delete this.intentsResolverResponsePromises[instanceId];
    }

    private handleRaiseOnError(instanceId?: string) {
        if (!instanceId) {
            return;
        }

        this.stopResolverInstance(instanceId);
    }

    private stopResolverInstance(instanceId: string): void {
        const gdWin = this.windows.findById(instanceId);

        gdWin?.close().catch((err) => this.logger.error(err));
    }

    private checkIfIntentsResolverIsEnabled(options: IOConnectDesktop.Config, appManager: IOConnectDesktop.AppManager.API): void {
        /* if Glue is initialized with { appManager: false }, fallback to previous implementation and do not use Intents Resolver UI */
        if (!appManager) {
            this.useIntentsResolverUI = false;
            return;
        }

        this.useIntentsResolverUI = typeof options.intents?.enableIntentsResolverUI === "boolean"
            ? options.intents.enableIntentsResolverUI
            : true;

        this.intentsResolverAppName = options.intents?.intentsResolverAppName ?? INTENTS_RESOLVER_APP_NAME;

        this.intentsResolverResponseTimeout = options.intents?.methodResponseTimeoutMs ?? DEFAULT_RESOLVER_RESPONSE_TIMEOUT;
    }

    private checkIfResolverShouldBeOpenedForRaise(intent: IOConnectDesktop.Intents.Intent, request: IOConnectDesktop.Intents.IntentRequest): ShouldResolverOpen {
        const checkOpen = this.checkIfIntentsResolverShouldBeOpened();

        if (!checkOpen.open) {
            return checkOpen;
        }

        const hasMoreThanOneHandler = this.checkIfIntentHasMoreThanOneHandler(intent, request);

        if (!hasMoreThanOneHandler) {
            return { open: false, reason: `Raised intent ${intent.name} has only one handler` };
        }

        return { open: true };
    }

    private checkIfResolverShouldBeOpenedForFilterHandlers(handlers: IOConnectDesktop.Intents.IntentHandler[], filter: IOConnectDesktop.Intents.HandlerFilter): ShouldResolverOpen {
        if (handlers.length === 1) {
            return { open: false, reason: `There's only one valid intent handler for filter ${JSON.stringify(filter)}` };
        }

        if (typeof filter?.openResolver === "boolean" && !filter.openResolver) {
            return { open: false, reason: "Intents resolver is disabled by IntentHandler filter" };
        }

        return this.checkIfIntentsResolverShouldBeOpened();
    }

    private checkIfIntentsResolverShouldBeOpened(): ShouldResolverOpen {
        if (!this.useIntentsResolverUI) {
            return { open: false, reason: `Intent Resolver is disabled. Resolving to first found handler` };
        }

        const intentsResolverApp = this.appManager.application(this.intentsResolverAppName);

        if (!intentsResolverApp) {
            return { open: false, reason: `Intent Resolver Application with name ${this.intentsResolverAppName} not found.` };
        }

        return { open: true };
    }

    private checkIfIntentHasMoreThanOneHandler(intent: IOConnectDesktop.Intents.Intent, request: IOConnectDesktop.Intents.IntentRequest): boolean {
        // no target => show resolver if request.handlers > 1 || intent.handlers.length > 1
        // reuse => show resolver if 2+ instances in request.handlers || show resolver if intent.handlers.length > 1
        // startNew => show resolver if 2+ apps in request.handler || intent.handlers as apps > 1
        // app => do not show resolver

        if (!request.target) {
            return request.handlers
                ? request.handlers.length > 1
                : intent.handlers.length > 1;
        }

        if (request.target === "reuse") {
            return request.handlers
                ? request.handlers.filter((handler) => handler.type === "instance" && handler.instanceId).length > 1 || request.handlers.filter((handler) => handler.type === "app").length > 1
                : intent.handlers.filter((handler) => handler.type === "instance" && handler.instanceId).length > 1 || intent.handlers.filter((handler) => handler.type === "app").length > 1;
        }

        if (request.target === "startNew") {
            return request.handlers
                ? request.handlers.filter((handler) => handler.type === "app").length > 1
                : intent.handlers.filter((handler) => handler.type === "app").length > 1;
        }

        if (typeof request.target === "object") {
            return false;
        }

        return false;
    }

    private buildInteropMethodName(intentName: string): string {
        return `${GLUE42_FDC3_INTENTS_METHOD_PREFIX}${intentName}`;
    }

    private clearUnregistrationPromise(promiseToRemove: Promise<void>): void {
        this.unregisterIntentPromises = this.unregisterIntentPromises.filter((promise) => promise !== promiseToRemove);
    }

    private unsubscribeIntent(intentName: string): void {
        this.myIntents.delete(intentName);

        const methodName = this.buildInteropMethodName(intentName);

        // typings are wrong and mark unregister as a sync method
        const unregisterPromise = this.interop.unregister(methodName) as unknown as Promise<void>;

        this.unregisterIntentPromises.push(unregisterPromise);

        unregisterPromise
            .then(() => {
                this.clearUnregistrationPromise(unregisterPromise);
            })
            .catch((err) => {
                this.logger.error(`Unregistration of a method with name ${methodName} failed with reason: `, err);

                this.clearUnregistrationPromise(unregisterPromise);
            });
    }

    private findHandlerByFilter(handlers: IOConnectDesktop.Intents.IntentHandler[], filter: { app?: string, instance?: string, type?: "app" | "instance" }) {
        if (filter.type) {
            return handlers.find((handler) => handler.type === filter.type);
        }

        if (filter.instance) {
            return handlers.find((handler) => filter.app
                ? handler.applicationName === filter.app && handler.instanceId === filter.instance
                : handler.instanceId === filter.instance
            );
        }

        if (filter.app) {
            return handlers.find((handler) => handler.applicationName === filter.app);
        }
    }

    private extractIntentsWithInfoByHandler(intents: IOConnectDesktop.Intents.Intent[], handler: IOConnectDesktop.Intents.IntentHandler): IOConnectDesktop.Intents.IntentInfo[] {
        const intentsWithInfo = intents.reduce((validIntentsWithInfo: IOConnectDesktop.Intents.IntentInfo[], intent: IOConnectDesktop.Intents.Intent) => {
            intent.handlers.forEach((currentHandler: IOConnectDesktop.Intents.IntentHandler) => {
                const isValid = Object.keys(handler).every((key) => {
                    return key === "contextTypes"
                        ? handler.contextTypes?.every((contextType: string) => currentHandler.contextTypes?.includes(contextType))
                        : (currentHandler as any)[key] === (handler as any)[key];
                });

                if (!isValid) {
                    return;
                }

                const intentWithInfo: IOConnectDesktop.Intents.IntentInfo = {
                    intent: intent.name,
                    contextTypes: currentHandler.contextTypes,
                    description: currentHandler.applicationDescription,
                    displayName: currentHandler.displayName,
                    icon: currentHandler.applicationIcon,
                    resultType: currentHandler.resultType
                };

                validIntentsWithInfo.push(intentWithInfo);
            });

            return validIntentsWithInfo;
        }, []);

        return intentsWithInfo;
    }
}
