import {
    default as CallbackRegistryFactory,
    CallbackRegistry,
    UnsubscribeFunction,
} from "callback-registry";
import { IOConnectCore } from "@interopio/core";
import { object, optional } from "decoder-validate";
import { AppManagerController } from "../appManager/controller";
import { BasePrefsConfig, PrefsHelloSuccess, operations } from "./protocol";
import { basePrefsConfigDecoder, nonEmptyStringDecoder, prefsOperationTypesDecoder } from "../shared/decoders";
import { GlueBridge } from "../communication/bridge";
import { IoC } from "../shared/ioc";
import { IOConnectBrowser } from "../../browser";
import { LibController, ParsedConfig } from "../shared/types";

export class PrefsController implements LibController {
    private bridge!: GlueBridge;
    private config!: ParsedConfig;
    private logger!: IOConnectBrowser.Logger.API;
    private appManagerController!: AppManagerController;
    private platformAppName!: string;
    private readonly registry: CallbackRegistry = CallbackRegistryFactory();

    public handlePlatformShutdown(): void {
        this.registry.clear();
    }

    public async start(coreGlue: IOConnectCore.API, ioc: IoC): Promise<void> {
        this.logger = coreGlue.logger.subLogger("prefs.controller.web");

        this.logger.trace("starting the web prefs controller");

        this.addOperationsExecutors();

        this.bridge = ioc.bridge;

        this.config = ioc.config;

        this.appManagerController = ioc.appManagerController;

        try {
            const prefsHelloSuccess = await this.bridge.send<void, PrefsHelloSuccess>("prefs", operations.prefsHello, undefined, undefined, { includeOperationCheck: true });

            this.platformAppName = prefsHelloSuccess.platform.app;
        } catch (error) {
            this.logger.warn("The platform of this client is outdated and does not support Prefs API.");
            return;
        }

        this.logger.trace("no need for platform registration, attaching the prefs property to glue and returning");

        const api = this.toApi();

        (coreGlue as IOConnectBrowser.API).prefs = api;
    }

    public async handleBridgeMessage(args: any): Promise<void> {
        const operationName = prefsOperationTypesDecoder.runWithException(args.operation);

        const operation = operations[operationName];

        if (!operation.execute) {
            return;
        }

        let operationData: any = args.data;

        if (operation.dataDecoder) {
            operationData = operation.dataDecoder.runWithException(args.data);
        }

        return await operation.execute(operationData);
    }

    private addOperationsExecutors(): void {
        operations.prefsChanged.execute = this.handleOnChanged.bind(this);
    }

    private toApi(): IOConnectBrowser.Prefs.API {
        const api: IOConnectBrowser.Prefs.API = {
            clear: this.clear.bind(this),
            clearAll: this.clearAll.bind(this),
            clearFor: this.clearFor.bind(this),
            get: this.get.bind(this),
            getAll: this.getAll.bind(this),
            set: this.set.bind(this),
            setFor: this.setFor.bind(this),
            subscribe: this.subscribe.bind(this),
            subscribeFor: this.subscribeFor.bind(this),
            update: this.update.bind(this),
            updateFor: this.updateFor.bind(this),
        };

        return api;
    }

    private async clear(): Promise<void> {
        const app = this.getMyAppName();

        await this.clearFor(app);
    }

    private async clearAll(): Promise<void> {
        await this.bridge.send<void, void>("prefs", operations.clearAll, undefined , undefined, { includeOperationCheck: true });
    }

    private async clearFor(app: string): Promise<void> {
        const verifiedApp = nonEmptyStringDecoder.runWithException(app);

        await this.bridge.send<BasePrefsConfig, void>("prefs", operations.clear, { app: verifiedApp }, undefined, { includeOperationCheck: true });
    }

    private async get(app?: string): Promise<IOConnectBrowser.Prefs.AppPreferences> {
        const verifiedApp = app === undefined || app === null ? this.getMyAppName() : nonEmptyStringDecoder.runWithException(app);

        const { prefs } = await this.bridge.send<BasePrefsConfig, { prefs: IOConnectBrowser.Prefs.AppPreferences }>("prefs", operations.get, { app: verifiedApp }, undefined, { includeOperationCheck: true });
        
        return prefs;
    }

    private async getAll(): Promise<{ all: IOConnectBrowser.Prefs.AppPreferences[] }> {
        const result = await this.bridge.send<void, { all: IOConnectBrowser.Prefs.AppPreferences[] }>("prefs", operations.getAll, undefined, undefined, { includeOperationCheck: true });

        return result;
    }

    private async set(data: any, options?: BasePrefsConfig): Promise<void> {
        const verifiedOptions = optional(basePrefsConfigDecoder).runWithException(options);
        const app = verifiedOptions?.app ?? this.getMyAppName();

        await this.setFor(app, data);
    }

    private async setFor(app: string, data: any): Promise<void> {
        const verifiedApp = nonEmptyStringDecoder.runWithException(app);
        const verifiedData = object().runWithException(data);

        await this.bridge.send<{ app: string, data: any }, void>("prefs", operations.set, { app: verifiedApp, data: verifiedData }, undefined, { includeOperationCheck: true });
    }

    private subscribe(callback: (prefs: IOConnectBrowser.Prefs.AppPreferences) => void): UnsubscribeFunction {
        const app = this.getMyAppName();

        return this.subscribeFor(app, callback);
    }

    private subscribeFor(app: string, callback: (prefs: IOConnectBrowser.Prefs.AppPreferences) => void): UnsubscribeFunction {
        const verifiedApp = nonEmptyStringDecoder.runWithException(app);
        const applications = this.appManagerController.getApplications();
        const isValidApp = verifiedApp === this.platformAppName || applications.some((application) => application.name === verifiedApp);

        if (!isValidApp) {
            throw new Error(`The provided app name "${app}" is not valid.`);
        }

        if (typeof callback !== "function") {
            throw new Error("Cannot subscribe to prefs, because the provided callback is not a function!");
        }

        const subscriptionKey = this.getSubscriptionKey(verifiedApp);

        this.get(verifiedApp).then(callback);

        return this.registry.add(subscriptionKey, callback);
    }

    private async update(data: any, options?: BasePrefsConfig): Promise<void> {
        const verifiedOptions = optional(basePrefsConfigDecoder).runWithException(options);
        const app = verifiedOptions?.app ?? this.getMyAppName();

        await this.updateFor(app, data);
    }

    private async updateFor(app: string, data: any): Promise<void> {
        const verifiedApp = nonEmptyStringDecoder.runWithException(app);
        const verifiedData = object().runWithException(data);

        await this.bridge.send<{ app: string, data: any }, void>("prefs", operations.update, { app: verifiedApp, data: verifiedData }, undefined, { includeOperationCheck: true });
    }

    private getMyAppName(): string {
        const myAppName = this.config.isPlatformInternal ? this.platformAppName : this.appManagerController.me?.application.name;

        if (!myAppName) {
            throw new Error("App Preferences operations can not be executed for windows that do not have app!");
        }
        
        return myAppName;
    }

    private getSubscriptionKey(app: string): string {
        return `prefs-changed-${app}`;
    }

    private async handleOnChanged({ prefs }: { prefs: IOConnectBrowser.Prefs.AppPreferences }): Promise<void> {
        const subscriptionKey = this.getSubscriptionKey(prefs.app);
        
        this.registry.execute(subscriptionKey, prefs);
    }
}
