import { IOConnectBrowser } from "../../browser";
import { wrappedIntentFilterDecoder, intentRequestDecoder, intentResultDecoder, wrappedIntentsDecoder, raiseIntentRequestDecoder, filterHandlersResultDecoder, filterHandlersWithResolverConfigDecoder, intentHandlerDecoder, getIntentsResultDecoder } from "../shared/decoders";
import { BridgeOperation } from "../shared/types";

export type IntentsOperationTypes = "getIntents" | "findIntent" | "raiseIntent" | "raise" | "filterHandlers" | "getIntentsByHandler";

export const operations: { [key in IntentsOperationTypes]: BridgeOperation } = {
    getIntents: { name: "getIntents", resultDecoder: wrappedIntentsDecoder },
    findIntent: { name: "findIntent", dataDecoder: wrappedIntentFilterDecoder, resultDecoder: wrappedIntentsDecoder },
    raiseIntent: { name: "raiseIntent", dataDecoder: intentRequestDecoder, resultDecoder: intentResultDecoder },
    raise: { name: "raise", dataDecoder: raiseIntentRequestDecoder, resultDecoder: intentResultDecoder },
    filterHandlers: { name: "filterHandlers", dataDecoder: filterHandlersWithResolverConfigDecoder, resultDecoder: filterHandlersResultDecoder },
    getIntentsByHandler: { name: "getIntentsByHandler", dataDecoder: intentHandlerDecoder, resultDecoder: getIntentsResultDecoder }
};

export interface WrappedIntentFilter {
    filter?: IOConnectBrowser.Intents.IntentFilter;
}

export interface WrappedIntents {
    intents: IOConnectBrowser.Intents.Intent[];
}

export interface IntentsResolverResponse {
    intent: string;
    handler: IOConnectBrowser.Intents.IntentHandler;
}

export interface IntentsResolverResponsePromise {
    intent: string;
    methodName: string;
    promise: Promise<IntentsResolverResponse>;
    resolve: (arg: IntentsResolverResponse) => void;
    reject: (reason: string) => void;
}

export interface RaiseIntentArgs extends IOConnectBrowser.Intents.IntentContext {
    _initialCallerId: string;
}