import { BridgeOperation } from "../shared/types";
import { basePrefsConfigDecoder, changePrefsDataDecoder, getAllPrefsResultDecoder, getPrefsResultDecoder, prefsHelloSuccessDecoder } from "../shared/decoders";

export type PrefsOperationTypes = "clear" | "clearAll" | "get" | "getAll" | "set" | "update" | "prefsChanged" | "prefsHello";

export const operations: { [key in PrefsOperationTypes]: BridgeOperation } = {
    clear: { name: "clear", dataDecoder: basePrefsConfigDecoder },
    clearAll: { name: "clearAll" },
    get: { name: "get", dataDecoder: basePrefsConfigDecoder, resultDecoder: getPrefsResultDecoder },
    getAll: { name: "getAll", resultDecoder: getAllPrefsResultDecoder },
    set: { name: "set", dataDecoder: changePrefsDataDecoder },
    update: { name: "update", dataDecoder: changePrefsDataDecoder },
    prefsChanged: { name: "prefsChanged", dataDecoder: getPrefsResultDecoder },
    prefsHello: { name: "prefsHello", resultDecoder: prefsHelloSuccessDecoder },
};

export interface BasePrefsConfig {
    app: string;
}

export interface PrefsHelloSuccess {
    platform: {
        app: string;
    };
}
