/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
import { IOConnectDesktop } from "@interopio/desktop";
import { IOConnectBrowser } from "../../browser";

export const enterprise = (config?: IOConnectBrowser.Config): IOConnectDesktop.API => {
    const enterpriseConfig = {
        windows: true,
        layouts: "full",
        appManager: "full",
        channels: true,
        libraries: config?.libraries ?? [],
        logger: config?.systemLogger?.level ?? "warn"
    };

    const injectedFactory = (window as any).IODesktop || (window as any).Glue;

    return injectedFactory(enterpriseConfig);
};